@media (orientation: portrait) {
  body {
    font-size: 16px;

    input[type="text"],
    input[type="button"] {
      font-size: inherit;
    }

    .root {
      justify-content: end !important;

      .widjet {
        width: 90% !important;
        box-shadow: none !important;
        input {
          height: 40px !important;
          font-size: 15px;
        }
        .menu {
          input[type="button"] {
            width: 30%;
          }
          input[type="text"] {
            width: 70%;
          }
        }
        .task {
          .view label {
            display: flex;
            flex-direction: column !important;
            width: 100% !important;

            .text_and_input-menu {
              display: flex;
              align-items: center;
              width: 100% !important;
              padding: 0 0 15px 0 !important;
              cursor: pointer;
            }
          }
          .button-menu {
            display: flex;
            width: 100% !important;
          }

          .edit {
            flex-direction: column !important;

            input[type="text"] {
              width: 100% !important;
              padding-bottom: 10px !important;
            }
          }

          .button-menu {
            display: flex;
            width: 100% !important;
          }
        }
      }
    }
  }
}
